.toolbar {
  margin-left: 2.6%;
  position: sticky;
  width: 100% !important;
  /* margin-left: 10px;
    box-shadow: 0 3px 5px rgb(0 0 0 / 20%); */
  height: 65px;
  top: 0px;
  left: 0px;
  /* z-index: 899; */
  background-color: white;
}
.toolbar .toolbar-navigation {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 5px 1px 5px 8px;
}
.searchBarInputt {
  width: 100%;
  height: 50px;
  border-radius: 7px;
  display: flex;
  padding-left: 15px !important;

  border: 1px solid #bdbdbd;
}
.toolbar-logo h1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 23px;
  line-height: 151.34%;
  color: #0d0d0d;
  display: flex;
  align-items: center;
}
.searchBarInputt input {
  width: 100%;
  height: 100%;
  padding: 9px;
  margin-left: 30px;
}
.searchBarInputt input:focus {
  outline: none;
}
.nav-items li {
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
}
.toolbar .toolbar-navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.toolbar-logo h2 {
  font-family: "Mitr";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  color: #1d4069;
  cursor: pointer;
}

.toolbar .toolbar-navigation-items li {
  padding: 5px 15px;
}
.toolbar .toolbar-logo {
  margin-left: 10px;
  display: flex;
}
.logo-text {
  margin-left: 10px;
  font-weight: 400;
  font-size: 30px;
  line-height: 36px;
  color: #1d4069;
}

.toolbar .toolbar-logo a {
  text-decoration: none;
}
.toolbar .toolbar-navigation-items a {
  text-decoration: none;
  color: #828282;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 700;
  transition: 0.3s;
}
.color-change {
  color: yellow !important;
}
.toolbar .toolbar-navigation-items a:hover,
.toolbar .toolbar-navigation-items a:active {
  opacity: 0.6;
  /* color: black !important; */
}
.nav-dropdown {
  background: #ffffff;
  box-shadow: 0px 0px 67px rgba(0, 0, 0, 0.03),
    0px 0px 39.2384px rgba(0, 0, 0, 0.0227778),
    0px 0px 21.3407px rgba(0, 0, 0, 0.0182222),
    0px 0px 10.8875px rgba(0, 0, 0, 0.015),
    0px 0px 5.45926px rgba(0, 0, 0, 0.0117778),
    0px 0px 2.63657px rgba(0, 0, 0, 0.00722222);
  border-radius: 15px;
  width: 181px;

  height: 85px;
  margin-top: 10px;
  margin-left: -120px;
  padding: 15px;
}
.nav-dropdown a {
  text-decoration: none !important;
  color: none;
}

.spacer {
  flex-grow: 1;
}

.login-btn-nav {
  width: 110px;
  height: 45px;
  color: #0d0d0d !important;
  margin-right: 10px;
  text-decoration: none;
  font-weight: 700;
}
.nav-line {
  /* border-right: 3px solid #381050; */
  opacity: 0.15;
  width: 15px;
  height: 45px;
  padding-left: 20px;
  /* padding-right: 50px; */
}
.register-btn-nav {
  width: 148px;
  height: 45px;
  color: #fff;
  border: 1px solid #002fa7;
  background: #002fa7;
  border-radius: 10px;
}
a {
  text-decoration: none !important;
  /* color: #381050 !important; */
}

@media (max-width: 760px) {
  .toolbar-navigation-items {
    display: none;
  }
  .toolbar-logo h1 {
    margin-left: 20px;
  }
}
@media (min-width: 761px) {
  .toggle-button {
    display: none;
  }
}
@media (max-width: 420px) {
  .toolbar-logo {
    width: auto;
    height: auto;
  }
  .toolbar .toolbar-navigation {
    padding: 0px;
  }
  .toggle-button img {
    width: 25px;
    height: auto;
  }
  .nav-dropdown {
    margin-left: -125px;
  }
}
@media (max-width: 350px) {
  .toolbar {
    height: auto;
  }
  .toolbar .toolbar-navigation {
    padding: 15px 10px;
  }
}
