}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(179, 179, 179);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: repeating-radial-gradient(
    circle,
    #0030a6 3px,
    #0030a6 3px,
    #0030a6 6px,
    #0030a6 6px
  );
}

::-webkit-scrollbar-thumb:hover {
  background: #0030a6;
}

html {
  scroll-behavior: smooth;
}

::selection {
  color: white;
  background: black;
}
.red {
  height: 15px;
  width: 15px;
  border-radius: 50%;
