* a:hover {
  color: none !important;
}
.daterangepicker td.active,
.daterangepicker td.active:hover {
  background-color: var(--primaryColor) !important;
}
.btn-primary {
  background-color: var(--primaryColor);
  font-size: 600 !important;
  color: white;
  border: none;
  border-radius: 3px;
}
.sectionScroll::-webkit-scrollbar {
  width: 2px;
}

.sectionScroll::-webkit-scrollbar-track {
  background-color: #e5e7eb;
  border-radius: 100px;
}

.sectionScroll::-webkit-scrollbar-thumb {
  height: 130px;
  border-radius: 100px;
  background-color: #002fa7;
  box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
}
:where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-disabled .ant-pagination-item-link, :where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-disabled:hover .ant-pagination-item-link {
display: flex;
align-items: center;
justify-content: center;
}
:where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-prev, :where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-next {
  display: flex;
align-items: center;
justify-content: center;
}
:where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-prev button, :where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-next button {
  display: flex;
  align-items: center;
  justify-content: center;
}
:where(.css-dev-only-do-not-override-1s3dcof).ant-modal .ant-modal-close:hover {
  background-color: transparent !important;
}
:where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-prev, :where(.css-dev-only-do-not-override-1s3dcof).ant-pagination .ant-pagination-next button {
  display: flex;
  align-items: center;
  justify-content: center;
}
 /* Swal Modal z-index */
 .swal2-container.swal2-top, .swal2-container.swal2-center, .swal2-container.swal2-bottom {
  z-index: 1000000 !important;
 }
.cancelBtn {
  background: none !important;
  border: 1px solid #b4b4b4 !important;
}

* input[type="checkbox"] {
  border: 1px solid rgba(220, 220, 225, 1);

  accent-color: var(--primaryColor);
}

.ant-table-container table > thead > tr th {
  background: #f1f5fb !important;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
  color: #333333 !important;
}
.ant-table-thead > tr > th {
  background: var(--primaryColor) !important;
  color: #fff !important;
}
.ant-pagination .ant-pagination-item-active a {
  color: var(--primaryColor) !important;
}
.ant-pagination .ant-pagination-item-active {
  border-color: var(--primaryColor) !important;
}

@media screen and (max-width: 620px) {
  .ant-table-content {
    overflow-x: scroll;
    width: 100%;
  }
}
@media screen and (max-width: 420px) {
  * h1 {
    font-size: 18px !important;
  }
  * label {
    font-size: 12px !important;
  }
}

.react-datepicker__day--selected,
.react-datepicker__day--in-selecting-range,
.react-datepicker__day--in-range,
.react-datepicker__month-text--selected,
.react-datepicker__month-text--in-selecting-range,
.react-datepicker__month-text--in-range,
.react-datepicker__quarter-text--selected,
.react-datepicker__quarter-text--in-selecting-range,
.react-datepicker__quarter-text--in-range,
.react-datepicker__year-text--selected,
.react-datepicker__year-text--in-selecting-range,
.react-datepicker__year-text--in-range {
